<template>
    <div>
        <div :style="{height:'800px',width:'100%'}" ref="myEchartMap"></div>
    </div>
</template>

<script>
    import echarts from "echarts";
    import '../../../node_modules/echarts/map/js/province/guangdong' // 
    import jiangmeng from './../../../public/data/440700'
    export default {
        name: "attendance",
        data() {
            return {
            }
        },
        mounted() {
            this.chinaConfigure();
            console.log(jiangmeng)
        },
        methods:{
            chinaConfigure() {
                // console.log(this.userJson)
                let myChart = echarts.init(this.$refs.myEchartMap); //
                // window.onresize = myChart.resize;
                echarts.registerMap('', jiangmeng);

                myChart.setOption({ // 
                    tooltip: {
                        trigger: 'item',
                        formatter: function loadData(result){//paramsAPI
                            //
                            // params.nameparams.valuejson
                            //params.data.value:json'value'
                            return result.name+'<br />:'+result.value;
                        }
                    },

                    visualMap: {
                        show : true,
                        x: 'left',
                        y: 'center',
                        splitList: [
                            {start: 1, end: 2,label: '1 - 2'},{start: 0, end:0,label: '0'},
                        ],
                        color: ['#548ECA', '#DCE5EE' ]
                    },
                    title:{
                        text:'',
                        //subtext:'',
                        x:'left',
                        y:'top',
                        textAlign:'left'
                    },
                    series: [{
                        type: 'map',
                        map: '' ,//echarts.registerMap
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true,
                                }
                            },
                            emphasis: {// 
                                areaColor: '#76A4D4',
                                label: {
                                    show: true,
                                    textStyle: {
                                        color: '#fff'
                                    }
                                }
                            }
                        },
                        data : [
                            {name:'',value:'2'},
                            {name:'',value:'0'},
                            {name:'',value:'0'},
                            {name:'',value:'1'},
                            {name:'',value:'1'},
                            {name:'',value:'2'},
                            {name:'',value:'0'},
                        ]//dataParam//[{name:'',value:'100'},{name:''value:'100'}......]
                    }]
                })
            },

        }
    }
</script>

